<template>
    <div class="transaction-record-list">
        <template v-if="recordList.length > 0">
            <div class="cell-part">
                <mt-cell @click.native="select" title="请选择查看交易类型" :value="selectOption.typeVal" is-link></mt-cell>
                <select-type ref="type" v-if="showPanel" :options="selectOption"></select-type>
            </div>
            <div class="cell-part">
                <template v-for="item in recordList">
                    <cell @click.native="detail(item)" :title="item.tran_type" :value="item.tran_amount" :label="item.tran_date" isLink>
                        <img src="../../../assets/img/life/power.png" slot="icon" width="24" height="24">
                    </cell>
                </template>
            </div>
        </template>
        <template v-else>
            <div class="none-record-list">
                <div class="bg">
                    <div class="bg-pic"></div>
                    <p class="bg-msg">暂无交易记录</p>
                </div>
            </div>
        </template>
    </div>
</template>

<script>
    import SelectType from './type';
    import * as services from '@/services/bank/transaction-record';
    export default {
        name: 'transaction-record-list',
        components: {
            SelectType
        },
        data() {
            return {
                recordList: [],
                selectAll: true,
                showPanel: false,
                selectOption: {
                    typeVal: '全部类型',
                    handler: (item) => {
                        this.panelCall(item);
                    },
                    content: [
                        { title: '全部类型', code: 'all' },
                        { title: '本人帐户互转', code: 'self-transfer' },
                        { title: '行内转账', code: 'bank-in' },
                        { title: '行外转账', code: 'bank-out' },
                        { code: 'large-amount', title: '定向大额转账' },
                        { code: 'ragular-live', title: '定活互转' },
                        { code: 'agriculture', title: '农信银行转账' }
                    ]
                }
            };
        },
        created() {
            let ls = this.$ls.getItem('phone-transaction-record');
            if (ls.length > 0) {
                this.recordList = ls;
                this.recordList.filter(item => {
                    item.tran_amount = item.tran_amount + '元';
                });
            }
        },
        methods: {
            detail(item) {
                let params = {
                    TRAN_NO: item.id
                };
                services.queryPhoneTransactionListDetail({ showLoading: '加载中...' })
                    .then(res => {
                        let _data = res.data.filter(item => {
                            return item.tran_print_no === params.TRAN_NO;
                        });
                        console.log(_data);
                        this.$ls.setItem('phone-transaction-record-detail', _data);
                        this.$router.push({ name: 'transactionRecrodInfo' });
                    }, err => {
                        console.log(err);
                    });
            },
            select() {
                this.showPanel = !this.showPanel;
            },
            panelCall(item) {
                this.showPanel = false;
                let ls = this.$ls.getItem('phone-transaction-record');
                if (item.code === 'all') {
                    this.recordList = ls;
                    this.selectAll = true;
                } else {
                    let _data = ls.filter(val => {
                        val.tran_amount = val.tran_amount + '元';
                        return val.code === item.code;
                    });
                    this.recordList = _data;
                    this.selectAll = false;
                }
                this.selectOption.typeVal = item.title;
            }
        }
    };

</script>

<style lang="scss">
.transaction-record-list {
    .field-part .cell-list .cell-wrap {
        padding: 40px 30px;
    }
    .none-record-list {
        padding-top: 318px;
        .bg {
            text-align: center;
            .bg-pic {
                margin: auto;
                width: 177.8px;
                height: 243.8px;
                @include image-set('../../../assets/img/bank/transaction/no-record');
            }
            .bg-msg {
                margin-top: 40px;
                font-size: 17px;
                color: #666666;
            }
        }
    }
}
</style>


