import ajax from '@/services/ajax';
import api from '@/services/api';

/*  查询手机交易记录   */
export function queryPhoneTransactionList(data) {
    return ajax.postAfterLoginQueryList('123456', data, api.queryPhoneTransactionList);
}

/*  查询手机交易记录   */
export function queryPhoneTransactionListDetail(data) {
    return ajax.postAfterLoginQueryList('123456', data, api.queryPhoneTransactionListDetail);
}


