<template>
    <div class="transaction-record">
        <div class="field-part">
            <cell-date-zone title="时间跨度" ref="dateZone" @date-zone-change=" dateZoneChange "></cell-date-zone>
            <mt-cell title="起始日期" :value=" dataForm.beginDate " class="cell-text-info">
            </mt-cell>
            <mt-cell title="结束日期" :value=" dataForm.endDate " class="cell-text-info">
            </mt-cell>
        </div>

        <div class="field-hint">
            仅支持查询时间跨三个月内的交易明细记录
        </div>

        <div class="field-action">
            <mt-button @click.native="search()" size="large" type="primary">查询</mt-button>
        </div>
    </div>
</template>

<script>
    import { Toast } from 'mint-ui-rem-dpr';
    import { mapGetters, mapActions, mapState } from 'vuex';
    import * as services from '@/services/bank/transaction-record';
    export default {
        data() {
            return {
                dataForm: {
                    account: '',
                    beginDate: '',
                    endDate: ''
                }
            };
        },
        mounted() {
            this.$refs.dateZone.setDefault(this.dataForm.beginDate, this.dataForm.endDate);
        },
        methods: {
            search() {
                services.queryPhoneTransactionList()
                    .then(res => {
                        this.$ls.setItem('phone-transaction-record', res.data);
                        this.$router.push({ name: 'transactionRecrodList' });
                    }, err => {
                        console.log(err);
                    });
            },
            dateZoneChange(begin, end) {
                this.dataForm.beginDate = begin;
                this.dataForm.endDate = end;
            }

        },
        beforeRouteLeave(to, from, next) {
            if (to.meta['groupName'] !== from.meta['groupName'] && to.meta['groupName'] !== 'selector') {
                this.$ls.removeItem('phone-transaction-record');
            }
            next(true);
        }
    };

</script>


