<template>
    <div class="type">
        <div class="cover"></div>
        <div class="main">
            <div class="panel-group">
                <div v-for="list in lists" @click="panelCall(list)" :class="{'select': selected === list.title}" class="select-panel">
                    {{list.title}}
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'type',
        props: {
            options: { type: Object, required: true }
        },
        computed: {
            selected() {
                return this.options.typeVal;
            },
            hand() {
                return this.options.handler;
            },
            lists() {
                return this.options.content;
            }
        },
        methods: {
            panelCall(list) {
                this.hand(list);
            }
        }
    };

</script>

<style lang="scss" scoped>
    $item-height: 80px;
    .type {
        position: relative;
        height: auto;
        z-index: 3;

        .main {
            position: absolute;
            padding: 42px 50px;
            top: 0;
            left: 0px;
            right: 0px;
            background-color: rgba(255, 255, 255, 0.95);
            box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);

            .panel-group {
                box-sizing: border-box;

                .select-panel {
                    display: inline-block;
                    
                    margin-bottom: 22px;
                    padding: 10px 5px;
                    width: 30.5%;
                    text-align: center;
                    border: 1px solid #999999;
                    border-radius: 6px;
                }
                .select-panel:nth-child(3n-1) {
                    margin: 0 4%;
                }

                .select {
                    color: #ff511a;
                    border: 1px solid #ff511a;
                }
            }
        }
    }
</style>


