<template>
    <div class="transaction-record-inf">
        <cell-map :dataCopy="recordInfo" :map="map"></cell-map>
    </div>
</template>

<script>
    export default {
        name: 'transaction-record-info',
        data() {
            return {
                recordInfo: {},
                map: [
                    { title: '交易类型', key: 'tran_type', divide: true, icon: true },
                    { title: '交易日期', key: 'tran_date' },
                    { title: '交易时间', key: 'tran_time' },
                    { title: '交易状态', key: 'tran_status', divide: true },
                    { title: '交易金额', key: 'tran_amount', type: 'amount-unit', style: {color: '#ff942f'} },
                    { title: '手续费', key: 'tran_fee' },
                    { title: '附言', key: 'remark', divide: true },
                    { title: '付款方户名', key: 'payment_name' },
                    { title: '付款方账号', key: 'payment_account' },
                    { title: '收款方户名', key: 'payee_name' },
                    { title: '收款方账号', key: 'payee_account', divide: true },
                    { title: '打印交易流水号', key: 'tran_print_no' }
                ]
            };
        },
        created() {
            let ls = this.$ls.getItem('phone-transaction-record-detail');
            this.recordInfo = ls[0];
        },
        beforeRouteLeave(to, from, next) {
            this.$ls.removeItem('phone-transaction-record-detail');
            next(true);
        }
    };

</script>


